.project-container {
  background-size: 400px auto;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  background-color: #e3e1e3;
}

.hoverbox {
  position: absolute;
  width: 100%;
  height: 400px;
}

.hoverbox:hover .text{
  display: block;
}

.text {
  display: none;
  position: absolute;
  background-color: white;
  height: 92%;
  width: 92%;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto
}

.text h1 {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.project-container a {
  width: 100%;
  height: 400px;
}

@media (max-width: 620px) {
  .project-container {
    background-size: 200px auto;
    background-position: center;
    margin: 0;
  }
  .hoverbox {
    width: 100%;
    height: 200px;
  }
}



